@mixin telefono() {
    @media (min-width: $telefono){
        @content; // Esto lo ponemos para poder inyectarle propiedades que queramos cuando lo invoquemos en otro archivo
    }
}

@mixin tablet{
    @media (min-width: $tablet){
        @content;
    }
}


@mixin desktop{
    @media (min-width: $desktop){
        @content;
    }
}

@mixin grid($columnas, $espaciado){
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}