.boletos{
    padding: $separacion 0;
    h3{
        text-align: center;
        color: $white;
    }

    .contenedor-boletos{
        margin-top: $separacion / 2;
        @include grid(2, $separacion);
    }

    .plan{
        p{
            color: $white;
            font-size: 3rem;
            text-align: center;
            margin-bottom: 2rem;
        }
    }
}

.pase{

    width: 100%;
    border-spacing: 0;

    thead{
        background-color: $rosa;

        th{
            padding: $separacion / 4;
            color: $white;
            text-transform: uppercase;
            font-weight: $delgada;
        }
    }

    tbody{
        background-color: $white;

        td{
            padding: $separacion / 4;
            border-bottom: 1px solid #e1e1e1;
        }

        tr{
            &:last-of-type{
                td{
                    border: none;
                }
            }
        }
    }

    tfoot{
        background-color: $morado;

            td{
                text-align: center;
                padding: $separacion / 4;
                color: $white;
                font-size: 3rem;
                font-weight: $delgada;
            }
    }
}