.informacion{
    padding: $separacion 0;

    @include tablet{
        @include grid(2, $separacion);
    }


    img{
        width: 100%;
    }
    
}

.informacion-texto{

    display: flex;
    flex-direction: column;

    .fecha{
        color: $rosa;
        font-weight: $bold;
    }

    .texto{
        line-height: 1.5;
    }

    h2{
        font-size: 3rem;
        
        margin-bottom: 5.5rem;
    }
}